.App {
  $cal-font-size: 36px;
  margin-top: 30px;
  padding: 20px;
  border-radius: 10px;
  width: 80vh;
  background-color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  color: cornflowerblue;
  .appTitle {
    width: 100%;
    span {
      width: calc($cal-font-size * 5);
      font-size: $cal-font-size;
      color: black;
    }
    #item_count {
      font-size: $cal-font-size;
      width: auto
    }
  }

  .cal-item {
    font-size: $cal-font-size;
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    .op-item {
      width: calc($cal-font-size * 2);
    }
    .op {
      width: $cal-font-size;
      color: darkblue;
    }
    .op-ret {
      font-size: $cal-font-size;
      width: calc($cal-font-size * 2);
      color: rgb(175, 5, 197);
    }
    .op-right {
      color: green;
    }
    .op-wrong {
      color: red;
    }
    .op-right-ret {
      margin-left: $cal-font-size;
      font-size: calc($cal-font-size/2);
      color: green;
    }
  }
}
